.item-1,
.item-2,
.item-3 {
  position: absolute;
  display: block;

  margin-left: 7%;
  width: 60%;
  font-weight: 300;
  font-size: 15px;
  font-family: "Noto Sans KR", sans-serif;
  animation-duration: 6s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  cursor: pointer;
}

.item-1 {
  animation-name: anim-1;
}

.item-2 {
  animation-name: anim-2;
}

.item-3 {
  animation-name: anim-3;
}

@keyframes anim-1 {
  0%,
  8.3% {
    top: 82%;
    opacity: 0;
  }
  8.3%,
  25% {
    top: 80%;
    opacity: 1;
  }
  33.33%,
  100% {
    top: 78%;
    opacity: 0;
  }
}

@keyframes anim-2 {
  0%,
  33.33% {
    top: 82%;
    opacity: 0;
  }
  41.63%,
  58.29% {
    top: 80%;
    opacity: 1;
  }
  66.66%,
  100% {
    top: 78%;
    opacity: 0;
  }
}

@keyframes anim-3 {
  0%,
  66.66% {
    top: 82%;
    opacity: 0;
  }
  74.96%,
  91.62% {
    top: 80%;
    opacity: 1;
  }
  100% {
    top: 78%;
    opacity: 0;
  }
}

.item-1 > h3,
.item-2 > h3,
.item-3 > h3 {
  all: unset;
  font-weight: 500;
  font-size: 17px;
  font-family: "Noto Sans KR", sans-serif;
  margin-right: 12px;
}



